.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.light-app {
    text-align: center;
}
.light-app > .r-segmented-control {
    background: #3b5988 !important;
}

button {
    padding: 7px 10px;
    margin: 0 5px;
    outline: 0;
    border: 1px solid #eae9ed;
    border-radius: 4px;
    cursor: pointer;
}

button.base {
    background: rgb(0,122,255);
    color: #fff;
}
button.dark {
    background: #333;
    color: #fff;
}
button.success {
    background: #5cb85c;
    color: #fff;
}
button.error {
    background: #d9534f;
    color: #fff;
}
button.light {
    background: rgb(247, 247, 247);
    color: #333;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



